.home-container{
    height: 100%;
    width: 100%;
}

.header {
	position: relative;
	width: 100%;
	min-height: 500px;
	height: 100vh;
	padding: 4.1vw 0;
	z-index: 1;
}

.header__wrapp {
	position: relative;
	display: flex;
	align-items: center;
	height: 100%;
}

.header__wrapp .content {
	position: relative;
	z-index: 2;
}

.header__title {
	text-transform: uppercase;
	font-size: 7.162vw;
}

.header__title span {
	display: block;
	color: var(--title-color);
	font-family: var(--font-monExtra);
	line-height: .63;
	letter-spacing: 28px;
}

.header__title_black {
	text-align: center;
}

.header__title span:nth-child(3) {
	text-align: right;
}

.header__title span:nth-child(1),
.header__title span:nth-child(3) {
	color: transparent;
	text-decoration: none;
	-webkit-text-stroke: 3px #000;
}

.header__img-wrapp {
	position: absolute;
	width: 100%;
	height: 100%;
}

.header__img-bg {
	position: relative;
	/* width: 400px; */
	width: 600px;
	height: 100%;
	padding: 60px 0;
	margin: 0 auto;
}

.header__let {
	position: absolute;
	top: 15px;
	left: -44px;
	font-family: var(--font-monExtra);
	text-transform: uppercase;
	color: #000;
	font-size: 80px;
}

.header__img-bg img {
	position: relative;
	width: 100%;
	height: 100%;
	object-fit: cover;
	z-index: 2;
}

.header__num {
	position: absolute;
	top: 60px;
	right: 5vw;
}

.header__num span {
	font-size: var(--font-size-num);
	font-family: var(--font-monExtra);
	font-style: italic;
	line-height: .9;
}

/**********************/
.header__bott {
	position: absolute;
	width: 100%;
	bottom: 0;
	left: 0;
}

.header__bott-wrapp {
	display: flex;
	justify-content: space-between;
}

.header__bott-scroll {
	position: relative;
}

.header-scroll {
	position: absolute;
	padding-right: 60px;
	left: 0;
	bottom: 0;
	background: none;
	border: none;
	font-size: var(--font-size-hed-soc);
	text-transform: uppercase;
	font-family: var(--font-monBlack);
	line-height: 1;
	transform-origin: right bottom;
	transform: translateX(-100%) rotate(90deg);
	-webkit-transform: translateX(-100%) rotate(90deg);
	-moz-transform: translateX(-100%) rotate(90deg);
	-ms-transform: translateX(-100%) rotate(90deg);
	-o-transform: translateX(-100%) rotate(90deg);
	cursor: pointer;
	transition: .3s;
}

.header-scroll:hover {
	padding-right: 70px;
	color: transparent;
	-webkit-text-stroke: 1px #000;
}

.header-scroll:before {
	position: absolute;
	content: '';
	width: 50px;
	height: 1.6787vw;
	top: 50%;
	right: 0;
	bottom: 0;
	background-image: url(../../../src/img/arrov.png);
	background-size: 100% 100%;
	transform: translateY(-50%);
}

.header__social-wrapp {
	display: flex;
}

.header__social-item {
	list-style: none;
}

.header__social-item+.header__social-item {
	margin-left: 30px;
}

.header__social-link {
	font-family: var(--font-monBlack);
	text-decoration: none;
	font-size: var(--font-size-hed-soc);
	text-transform: uppercase;
	color: #000;
	transition: .3s;
}

.header__social-link:hover {
	color: transparent;
	-webkit-text-stroke: 1px #000;
}

.main {
	position: relative;
	z-index: 1;
}

/******** section-popular-models ********/
.popular__wrapp {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	width: 100%;
	max-width: 1070px;
	
	margin: 0 auto;
}

.popular__item {
	position: relative;
	width: 47.5%;
	max-width: 500px;
	height: 500px;
	
	margin-bottom: 3.2vw;
	overflow: hidden;
}

.displacement {
	position: absolute;
	display: block;
	width: 500px;
	height: 500px;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	object-fit: cover;
}

.js-loadme canvas {
	width: 100%;
	height: 100%;
	object-fit: cover;
}



.popular__item:nth-child(3),
.popular__item:nth-child(4) {
	margin-bottom: 0;
}

.popular__name {
	position: absolute;
	bottom: -12px;
	left: -12px;
	color: transparent;
	line-height: .8;
	-webkit-text-stroke: 1px #fff;
	font-size: 4.95vw;
	text-transform: uppercase;
	font-family: var(--font-monBlack);
}

/******** section-about ********/
.about__wrapp {
	display: flex;
	justify-content: space-between;
	width: 100%;
	height: auto;
}

.about__img {
	position: relative;
	width: 40%;
	padding: 45px;
}

.about__span-frame {
	position: absolute;
	width: 80px;
	height: 80px;
	border: 2px solid #000;
}

.about__span-frame:nth-child(2),
.about__span-frame:nth-child(3) {
	top: 0;
	border-bottom: transparent;
}

.about__span-frame:nth-child(2),
.about__span-frame:nth-child(4) {
	left: 0;
	border-right: transparent;
}

.about__span-frame:nth-child(4),
.about__span-frame:nth-child(5) {
	bottom: 0;
	border-top: transparent;
}

.about__span-frame:nth-child(3),
.about__span-frame:nth-child(5) {
	right: 0;
	border-left: transparent;
}

.about__img-wrapp {
	width: 100%;
}

.about__img-wrapp img {
	display: block;
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.about__txt {
	width: 50%;
	padding-top: 60px;
}

.about__txt p {
	font-size: var(--font-size-txt);
	text-transform: uppercase;
	line-height: var(--line-height-txt);
}


/******** section-top-qualities ********/
.top__wrapp {
	display: flex;
	justify-content: space-between;
	width: 100%;
}

.top__txt {
	width: 45%;
}

.top__item {
	width: 100%;
	margin-bottom: 60px;
	cursor: pointer;
	user-select: none;
}

.top__item.active {
	pointer-events: none;
}

.top__head h3,
.top__head h3 span {
	line-height: 1;
	font-size: var(--font-size-bl);
	font-family: var(--font-monExtra);
}

.top__head_hover,
.top__head_actve {
	transition: .3s;
}

.top__item:hover .top__head_hover,
.top__item.active .top__head_hover,
.top__item.active .top__head_actve {
	color: transparent;
	-webkit-text-stroke: 1px #000;
}

.top__descript {
	display: none;
	height: auto;
	padding-top: 30px;
}

.top__descript p {
	text-transform: uppercase;
	font-size: var(--font-size-txt);
	line-height: var(--line-height-txt);
}

/*****************/
.top__img {
	position: relative;
	width: 45%;
	height: 650px;
	overflow: hidden;
}

.top__img-line {
	position: relative;
	width: 100%;
	height: 100%;
	z-index: 1;
}

.top__img-line:before,
.top__img-line:after {
	position: absolute;
	content: '';
	width: 1000%;
	height: 2px;
	background: #000;
	top: 50%;
	left: 50%;
}

.top__img-line:before {
	transform: translate(-50%, -50%) rotate(49.5deg);
}

.top__img-line:after {
	transform: translate(-50%, -50%) rotate(-49.5deg);
}

.top__img-wrapp {
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	border: 2px solid #000;
	z-index: 2;
}

.top__img-item {
	position: absolute;
	display: block;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	opacity: 0;
	overflow: hidden;
	transform: scaleY(.4);
	transition: .8s cubic-bezier(.9, 0, .1, 1);
}

.top__img-item.active {
	opacity: 1;
	transform: scaleY(1);
}

.top__img-item img {
	display: block;
	width: 100%;
	height: 100%;
	object-fit: cover;
}

/******** section-modern-design ********/
.modern__wrapp,
.modern__item {
	width: 100%;
}

.modern__item {
	display: flex;
	justify-content: space-between;
}

.modern__img {
	width: 45%;
}

.modern__img-wrapp {
	display: inline-block;
	width: 100%;
}

.modern__img-wrapp img {
	display: inline-block;
	max-width: 500px;
	width: 100%;
}

/********************/
.modern__item:nth-child(2) {
	margin: 120px 0;
}

.modern__img.right {
	text-align: right;
}

.modern__txt {
	width: 45%;
	padding-top: 60px;
}

.modern__txt h3 {
	margin-bottom: 30px;
	text-align: right;
	line-height: 1;
	font-size: var(--font-size-bl);
	font-family: var(--font-monExtra);
}

.modern__txt.left h3 {
	text-align: left;
}

.modern__txt p {
	font-size: var(--font-size-txt);
	line-height: var(--line-height-txt);
	text-transform: uppercase;
}

/******** section-brends ********/
.brends__wrapp {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	width: 100%;
	height: auto;
}

.brends__item {
	display: flex;
	align-items: center;
	width: 21.528%;
	height: 150px;
	padding: 0 40px;
	margin-bottom: 60px;
	background-color: #ededed;
}

.brends__item img {
	display: block;
	width: 100%;
}

/******** section-get-in-touch ********/
.getin__wrapp {
	padding-bottom: 100px;
}

.getin__item {
	list-style: none;
}

.getin__item+.getin__item {
	margin-top: 30px;
}

.getin__link {
	position: relative;
	display: inline-block;
	padding-right: 170px;
	font-family: var(--font-monExtra);
	font-size: var(--font-size-bl);
	color: #000;
	transition: .3s;
}

.getin__link:hover {
	padding-right: 180px;
	color: transparent;
	-webkit-text-stroke: 1px #000;
}

.getin__link:after {
	position: absolute;
	content: '';
	width: 90px;
	height: 35px;
	top: 50%;
	right: 0;
	transform: translateY(-50%);
	background-image: url(../../../src/img/arrov-link.png);
	background-repeat: no-repeat;
	background-size: 100% 100%;
}

/******** footer ********/
.footer {
	width: 100%;
	height: auto;
	padding: 30px 10px;
	background-color: #ededed;
	text-align: center;
}

.footer span {
	font-size: 24px;
	color: #646464;
}


/* 3.0.media(max-width: 1600px) *********************/
/**************************************************/
@media(max-width: 1600px) {
	.header__img-bg {
		width: 430px;
	}
}


/* 3.1.media(max-width: 1200px) *********************/
/**************************************************/
@media(max-width: 1200px) {
	.menu {
		padding: 20vh 0 25vh;
	}

	.header__img-bg {
		width: 400px;
	}

	.brends__item {
		width: 27%;
	}

	.brends__item:nth-child(7),
	.brends__item:nth-child(8) {
		margin-bottom: 0;
	}
}


/* 3.2.media(max-width: 1100px) *********************/
/***************************************************/
@media(max-width: 1100px) {
	:root {
		--font-size-hed-soc: 2.22vw;
		--font-size-num: 5.6vw;
		--font-size-txt: 1.6vw;
		--font-size-bl: 3.2vw;
		--line-height-txt: 1.6;
	}

	.header__title span:nth-child(1),
	.header__title span:nth-child(3),
	.section-title__item+.section-title__item {
		-webkit-text-stroke: 1.5px #000;
	}

	.section-title {
		margin-bottom: 115px;
	}

	/*****************/
	.top__item {
		margin-bottom: 35px;
	}

	.top__img {
		height: 510px;
	}

	/*****************/
	.modern__item:nth-child(2) {
		margin: 70px 0;
	}

	/*****************/
	.footer {
		padding: 20px 10px;
	}

	.footer span {
		font-size: 15px;
	}

}

/* 3.2.media(max-width: 900px) *********************/
/**************************************************/
@media(max-width: 900px) {
	:root {
		--font-size-hed-soc: 2.8vw;
	}

	._padd_top-bott {
		padding: 30px 0;
	}

	/*************************/
	.menu-close {
		font-size: 30px;
	}

	/*************************/
	.burger {
		width: 42px;
		height: 15px;
	}

	/*************************/
	.header__let {
		top: 23px;
		left: -30px;
		font-size: 60px;
	}

	.header__title {
		font-size: 6.162vw;
	}

	.header__title span {
		letter-spacing: 20px;
	}

	/*************************/
	.section-title__wrapp {
		padding-top: 85px;
		font-size: 5.8vw;
	}

	.section-title {
		margin-bottom: 80px;
	}

	/**************************/
	.about__img {
		padding: 25px;
	}

	.about__span-frame {
		width: 50px;
		height: 50px;
	}

	/**************************/
	.top__img {
		height: 400px;
	}

	/**************************/
	.brends__item {
		width: 45%;
	}

	/**************************/
	.getin__wrapp {
		padding-bottom: 50px;
	}

	.getin__link {
		padding-right: 100px;
	}

	.getin__link:hover {
		padding-right: 110px;
	}

	.getin__link:after {
		width: 65px;
		height: 25px;
	}
}

/* 3.4.media(max-width: 650px) ********************/
/*************************************************/
@media(max-width: 650px) {
	:root {
		--font-size-hed-soc: 3.8vw;
		--font-size-txt: 2vw;
		--font-size-bl: 4.2vw;
	}

	.header__img-bg {
		width: 300px;
	}

	.header__title {
		font-size: 8.162vw;
	}

	.header__title span {
		letter-spacing: 6px;
	}

	/*********************/
	.section-title {
		margin-bottom: 50px;
	}

	.section-title__wrapp {
		font-size: 6.5vw;
	}

	/*********************/
	.displacement {
		width: 250px;
		height: 250px;
	}

	.popular__item {
		height: 250px;
	}

	.popular__name {
		bottom: -7px;
	}

	/*********************/
	.about__txt {
		padding-top: 23px;
	}

	/*********************/
	.top__item {
		margin-bottom: 20px;
	}

	.top__txt {
		width: 100%;
	}

	.top__descript {
		padding-top: 15px;
	}

	.top__img {
		display: none;
	}

	/**********************/
	.modern__txt h3 {
		margin-bottom: 15px;
	}

	.modern__txt {
		padding-top: 30px;
	}

	.modern__item:nth-child(2) {
		margin: 30px 0;
	}

	/**********************/
	.getin__link {
		padding-right: 70px;
	}

	.getin__link:hover {
		padding-right: 80px;
	}

	.getin__link:after {
		width: 50px;
		height: 17px;
	}
}

/* 3.5.media(max-width: 500px) ********************/
/*************************************************/
@media(max-width: 500px) {
	:root {
		--font-size-hed-soc: 4.8vw;
		--font-size-num: 6.6vw;
		--font-size-txt: 2.5vw;
		--font-size-bl: 5.2vw;
	}

	/***********************/
	.menu-close {
		font-size: 22px;
	}

	.burger {
		width: 35px;
		height: 12px;
	}

	/***********************/
	.header__num {
		right: 5vw;
	}

	.header__title span:nth-child(1),
	.header__title span:nth-child(3) {
		-webkit-text-stroke: 1px #000;
	}

	.header__img-bg {
		width: 60%;
		padding: 90px 0;
	}

	.header-scroll:before {
		width: 26px;
	}

	/*********************/
	.section-title__wrapp {
		padding-top: 47px;
	}

	.section-title__item+.section-title__item {
		-webkit-text-stroke: 1px #000;
	}

	/**********************/
	.about__wrapp {
		flex-wrap: wrap;
	}

	.about__img,
	.about__txt {
		width: 100%;
	}

	.about__img {
		padding: 45px;
	}

	/**********************/
	.getin__link {
		padding-right: 0;
	}

	.getin__item+.getin__item {
		margin-top: 15px;
	}

	.getin__link:after {
		display: none;
	}
}

/* 3.6.media(max-width: 400px) ********************/
/*************************************************/
@media(max-width: 400px) {
	.header {
		max-height: 667px;
	}

	.header__img-bg {
		padding: 160px 0;
	}

	.header__let {
		top: 139px;
		left: -15px;
		font-size: 34px;
	}

	/*******************/
	.section-title__wrapp {
		padding-top: 34px;
	}

	/*******************/
	.displacement {
		width: 150px;
		height: 150px;
	}

	.popular__item {
		height: 150px;
	}

	/*******************/
	.about__img {
		padding: 25px;
	}

	.about__img-wrapp {
		height: 230px;
	}

	/*******************/
	.top__item:last-child {
		margin-bottom: 0;
	}

	/******************/
	.modern__item {
		flex-wrap: wrap;
	}

	.modern__img,
	.modern__txt {
		width: 100%;
	}

	.modern__item:nth-child(1) .modern__img,
	.modern__item:nth-child(3) .modern__img {
		order: 2;
	}

	.modern__item:nth-child(1) .modern__txt,
	.modern__item:nth-child(2) .modern__txt,
	.modern__item:nth-child(3) .modern__txt {
		padding-top: 0;
		padding-bottom: 15px;
	}

	.modern__item:nth-child(2) {
		margin: 40px 0;
	}

	/********************/
	.brends__item {
		height: 64px;
		padding: 0 25px;
		margin-bottom: 25px;
	}
}

/***======================= 3.MEDIA-SETTINGS-END =======================***/
/***===================================================================***/
