
[data-aos="anim_title"] {
	opacity: 0;
	transform: translateY(-100%);
}

[data-aos="anim_title"].aos-animate {
	opacity: 1;
	transform: translateY(0);
}

[data-aos="anim_left"] {
	opacity: 0;
	transform: translateX(-5%);
}

[data-aos="anim_left"].aos-animate {
	opacity: 1;
	transform: translateX(0);
}

[data-aos="anim_scale"] {
	opacity: 0;
	transform: scaleX(.6);
}

[data-aos="anim_scale"].aos-animate {
	opacity: 1;
	transform: scaleX(1);
}

[data-aos="anim_height"] {
	opacity: 0;
	height: 0;
}

[data-aos="anim_height"].aos-animate {
	opacity: 1;
	height: 100%;
}

.nav {
	position: fixed;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 4.1vw;
	top: 0;
	right: 0;
	left: 0;
	z-index: 2;
	transition: .3s;
}

.nav.active {
	background-color: #fff;
	padding-top: 10px !important;
	padding-bottom: 10px !important;
	box-shadow: 0 -7px 24px -15px #000;
}

.logo {
	text-transform: uppercase;
	font-family: var(--font-monBlack);
	font-size: var(--font-size-hed-soc);
	line-height: 1;
}

.burger {
	position: relative;
	display: inline-block;
	width: 50px;
	height: 20px;
	background: none;
	border: none;
	cursor: pointer;
}

.burger__line {
	position: absolute;
	display: inline-block;
	width: 100%;
	height: 3px;
	left: 0;
	background: var(--background-black);
	transition: .3s;
}

.burger:hover .burger__line {
	opacity: .5;
}

.burger__line:nth-child(1) {
	top: 0;
}

.burger__line:nth-child(2) {
	bottom: 0;
}


.menu-close {
	position: absolute;
	top: 0;
	right: 0;
	padding: 40px;
	font-size: 40px;
	color: #000;
	cursor: pointer !important;
	background: none;
	border: none;
	outline: none;
	transition: .3s;
    z-index: 3;
}

.menu-close:hover {
	color: transparent;
	-webkit-text-stroke: 1px #000;
}

.socials {
	position: absolute;
	bottom: 0;
	left: 0;
	margin: 40px 6vw;
}

.socials a {
	margin: 0 20px;
	color: #000;
	text-decoration: none;
    
	text-transform: uppercase;
}

.menu-container {
	position: fixed;
	left: -100%;
	width: 100%;
	height: 100vh;
	background: #fff;
	z-index: 3;
}

.secondary-text-logo{
	margin-top: 100%;
	font-size: 2rem;
}

.menu__item {
	position: relative;
	padding: 0 6vw;
}

.menu__item-link {
	position: relative;
	display: inline-block;
	cursor: pointer;
	color: #000;
	text-decoration: none;
	transition: opacity 0.4s;
}

.menu__item-link::before {
	all: initial;
	position: absolute;
	bottom: 60%;
	left: 0;
	pointer-events: none;
}

.menu__item-link:hover {
	transition-duration: 0.1s;
	opacity: 0;
}

.menu__item-img {
	position: absolute;
	height: 12vh;
	max-height: 600px;
	pointer-events: none;
	opacity: 0;
	top: 10%;
	left: 8%;
	transform: scale(0);
	z-index: 2;
}

.menu__item-link:hover+.menu__item-img {
	opacity: 1;
	transform: scale(1.3);
	transition: all 0.4s;
}

.menu {
	padding: 12vh 0 25vh;
	--offset: 20vw;
	--move-initial: calc(-25% + var(--offset));
	--move-final: calc(-50% + var(--offset));
	font-size: 7vw;
}

.marquee {
	position: absolute;
	top: 0;
	left: 0;
	overflow: hidden;
	color: rgb(214, 214, 214);
	pointer-events: none;
}

.marquee__inner {
	position: relative;
	display: flex;
	width: fit-content;
	opacity: 0;
	transition: all 0.1s;
	transform: translateX(60px);
}

.menu__item-link:hover~.marquee .marquee__inner {
	opacity: 1;
	transform: translateX(0px);
	transition-duration: 0.4s;
}

.menu__item-link,
.marquee span {
	white-space: nowrap;
	font-size: 7vw;
	padding: 0 1vw;
	line-height: 1.15;
}

.marquee span {
	font-style: italic;
}

